@use "global" as *;

.about-page__inner {
    position: relative;
}

.about-page__images {
    display: flex;
    max-width: rem(1080);
    width: 100%;
    margin-top: rem(32);
    position: relative;
    @include mq(md){
        margin-top: rem(43);
    }
}

.about-page__image-1 {
    display: none;
    @include mq(md){
        display: block;
        width: 37.1%;
        max-width: 100%;
        position: relative;
        margin-bottom: rem(55);
        aspect-ratio: 400 / 606;
    }
}

.about-page__image-1 img {
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.about-page__image-2 {
    width: 100%;
    max-width: 100%;
    aspect-ratio: 345 /228;
    @include mq(md){
        width: 81.4%;
        display: block;
        margin-top: rem(82);
        margin-left: rem(-190);
        aspect-ratio: 880 / 581;
    }
}

.about-page__image-2 img {
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.about-page__container {
    margin-top: rem(40);
    @include mq(md){
        margin-top: 0;
        max-width: rem(905);
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: rem(40);
        padding-right: rem(65);
        position: absolute;
        top: 32%;
        right: 0;
    }
}

.about-page__head {
    color: $base-color;
    font-size: rem(40);
    font-family: $gotu-font-family;
    line-height: 1;
    position: absolute;
    top: 10%;
    left: 13%;
    @include mq(md){
        font-size: rem(80);
        line-height: 1.25;
        width: 47.6%;
        position: static;
    }
}

.about-page__wrapper {
    @include mq(md){
        width: 47.6%;
    }
}

.about-page__text {
    margin-top: rem(16);
    @include mq(md){
        margin-top: 0;
        color: $base-color;
    }
}

.about-page__decoration {
    display: block;
    position: absolute;
    width: rem(120);
    top: rem(-35);
    left: max(calc(50% - 49vw),-3.4375rem);
    @include mq(md){
        width: rem(301);
        top: rem(-68);
    }
}

